import { currentWeek, todayYmd } from "./util";

export default {
  methods: {
    load() {
      const s = localStorage.getItem("accounts");
      if (!s) {
        const as = window.prompt("Accounts: ");
        this.accounts = as.split(",");
        localStorage.setItem("accounts", JSON.stringify(this.accounts));
      } else {
        this.accounts = JSON.parse(s);
      }
      this.date = todayYmd();
      this.week = currentWeek(new Date());
      const t = localStorage.getItem("trackers-" + this.date) ?? "[]";
      this.trackers = JSON.parse(t);
      const wt = localStorage.getItem("w-trackers-" + this.week) ?? "[]";
      this.weekTrackers = JSON.parse(wt);
    },
    clear() {
      localStorage.removeItem("accounts");
      window.location.reload();
    },
  },
  mounted() {
    console.log(this.accounts);
  },
  data() {
    return {
      accounts: [],
      events: [
        "Điểm danh",
        "Cầu phúc",
        "Góp quân tư",
        "Thần thú",
        "Thí luyện AH",
        "Full phó bản",
        "Full Q",
        "Treo thưởng",
        "Gia viên nuôi trồng",
      ],
      weekEvents: [
        "Hiệp khách",
        "Cầu nguyện",
        "Quà khởi công",
        "Trống chinh chiến",
        "Đổi rương",
        "Nuôi thủ hộ thú",
        "Ôn tuyền lần 1",
        "Ôn tuyền lần 2",
        "Ôn tuyền lần 3",
      ],
      date: null,
      week: null,
      trackers: [],
      weekTrackers: [],
    };
  },
  watch: {},
};
